.btn-grad {background-image: linear-gradient(to right, #02AAB0 0%, #00CDAC  51%, #02AAB0  100%)}
.btn-grad {
   margin: 10px;
   padding: 15px 30px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color:white;            
   box-shadow: 0 0 10px #eee;
   border-radius: 10px;
   display: block;
   width: 180px;
   font-family:sans-serif;
   margin-left:auto;
   margin-right:auto;
 }

 .App-wrap-button{
  margin-left:auto;
  margin-right:auto;
 }
.btn-grad-coming {background-image: linear-gradient(to right, #262727 0%, #3c0b4b  51%, #aa30c9  100%)}
.btn-grad-coming {
   margin: 10px;
   padding: 15px 30px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color:white;            
   box-shadow: 0 0 10px #eee;
   border-radius: 10px;
   display: block;
   width: 180px;
   font-family:sans-serif;
   margin-left:auto;
   margin-right:auto;
 }
 .btn-grad:hover {
   background-position: right center; /* change the direction of the change here */
   color: white;
   text-decoration: none;
 }
